import React from 'react';
import { connect } from 'react-redux';
import AnimateHeight from 'react-animate-height';

class Month extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      display: false
    };
  }

  handleMainDisplay() {
    this.setState({
      display: ! this.state.display
    });
  }
  
  render() {
    let mainHeight = 0;
    let iconDirection = <i className="fas fa-chevron-down"></i>
    if (this.state.display) {
      mainHeight = 'auto';
      iconDirection = <i className="fas fa-chevron-up"></i>
    }
    let hearingLinks = [];
    for (const hearing of this.props.hearings) {
      console.log(JSON.stringify(hearing, null, 4));
      if (hearing.morningVideo !== undefined && hearing.afternoonVideo !== undefined) {
        let morningSession;
        let devURL = (this.props.isDev) ? 'dev=t&' : '';
        if (hearing.morningVideo !== '') {
          morningSession = <a href={'/schedule/?' + devURL + 'h=' + hearing.timeStamp + '&v=m'} className="ArchiveVideoLink">Watch Morning Session</a>;
        } else {
          morningSession = <span style={{fontWeight: '400'}}>No Morning Session Video</span>;
        }
        let afternoonSession;
        if (hearing.afternoonVideo !== '') {
          afternoonSession = <a href={'/schedule/?' + devURL + 'h=' + hearing.timeStamp + '&v=a'} className="ArchiveVideoLink">Watch Afternoon Session</a>;
        } else {
          afternoonSession = <span style={{fontWeight: '400'}}>No Afternoon Session Video</span>;
        }
        hearingLinks.push(<p key={hearing.timeStamp} className="ArchiveVideoTitle">{new Date(hearing.timeStamp).format('F d')}: {morningSession} - {afternoonSession}</p>);
      }
    }
    return (
      <div className="WebArchiveMonthDiv" onClick={this.handleMainDisplay.bind(this)}>
        <h3 style={{float: 'right'}}>{iconDirection}</h3>
        <h3>{this.props.name}</h3>
        <AnimateHeight duration={500} height={mainHeight}>
          {hearingLinks}
        </AnimateHeight>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isDev: state.isDev,
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Month);